<template>
  <div class="joke">
    <el-row>
      <el-row v-for="item in jokes" :key="item.uid" style="margin: 0 auto;width: 60%;">
        <el-row>
          <el-row style="padding:20px 20px; line-height: 30px; font-size: 18px;">{{item.text}}</el-row>
          <video v-if="item.type==='video'" :src="item.video" controls
                 style="width:200px; height:200px; margin: 0 auto;"></video>
          <img v-if="item.type==='image'" :src="item.image" alt="" width="200px">
          <img v-if="item.type==='gif'" :src="item.gif" alt="" width="200px">
        </el-row>
      </el-row>
    </el-row>
    <el-row type="flex" justify="center">
      <el-button type="primary" @click="getData">刷新</el-button>
      <el-button type="primary" @click="getRealData">最新段子</el-button>
    </el-row>
    <el-row class="mobilePage">
      我只在小于400px的屏幕上显示
    </el-row>
  </div>
</template>
<script>
import { getArticleDate } from '@/api/oneArticle'
import { getLatestGank, getSearchGank } from '@/api/gank'
import { getJoke } from '@/api/juhe'
import { getLatestMovie } from '@/api/douban'
import { getRandomJoke, getRealTimeJoke } from '@/api/apiopen'

export default {
  name: 'joke',
  data () {
    return {
      jokes: [],
    }
  },
  mounted () {
    this.getData()
  },
  methods: {
    getData () {
      getRandomJoke()
        .then(res => {
          if (res.code === 200) {
            this.jokes = res.data
          } else {
            this.$message.error(res)
            console.log(res)
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    getRealData () {
      getRealTimeJoke()
        .then(res => {
          if (res.code === 200) {
            this.jokes = res.data
          } else {
            this.$message.error(res)
            console.log(res)
          }
        })
        .catch(err => {
          console.log('err', err)
        })
    }
  }
}
</script>
<style lang="scss">
.joke {
  background-color: #fff;

  .mobilePage {
    display: none;
  }
}


@media screen and (max-device-width: 400px) {
  // 真实宽度小于400px (非web端窗口大小)
  .mobilePage {
    display: block;
  }
}
</style>
